// Default
html {
  &.on-fancybox {
    overflow: hidden;
  }
}

body {
  margin: 0;
  font-size: 62.5%;
  color: $colortext;
  padding: 0;
  text-align: left;
  background-color: $white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
  font-family: $text;
  box-sizing: border-box;
  position: relative;
  &.fancybox-active{
    overflow: hidden;
  }
  &.nav-fixed {
    padding-top: 150px;
  }
  &.wp_activities-template-default {
    .header {
      #menu-item-254 {
        a {
          color: $blue !important;
          &::after {
            background-color: $blue !important;
            @media (max-width: 991px) {
              opacity: 1 !important;
            }
          }
        }
      }
    }
  }
  &.atividades, &.wp_activities-template-default {
    .header {
      .toggle {
        background-color: $blue !important;
      }
      .ball > svg > path {
        fill: $blue;
        opacity: .25;
      }
    }
  }
  &.documentarios, &.home {
    .header{ 
      .toggle {
        background-color: $purple !important;
      }
      .ball > svg > path {
        fill: $purple;
        opacity: .25;
      }
    }
  }
  &.ideias-da-juventude {
    .header{
      .toggle {
        background-color: $yellow !important;
      }
      .ball > svg > path {
        fill: $yellow;
        opacity: .25;
      }
    }
  }
  &.jogo-da-diversidade {
    .header{ 
      .toggle {
        background-color: $orange !important;
      }
      .ball > svg > path {
        fill: $orange;
        opacity: .25;
      }
    }
  }
}

html,
body {
  min-height: 100vh;
  overflow-x: hidden;
}

img {
  display: inline-block;
  max-width: 100%;
  width: auto;
  height: auto;
  border-radius: 6px;
}

blockquote {
  margin-left: 20px;
  font-weight: bold;
  padding: 15px;
  margin-top: 15px;

  p {
    font-style: italic;
    @include font-size(22px);

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

table {
  font-weight: 400;
  margin-top: 15px;
  margin-bottom: 0;
  line-height: 1.6em;
  @include font-size(16px);
  @extend .table;
}

h1 {
  @include font-size(36px);
  font-family: $title;
}

h2 {
  @include font-size(30px);
  font-family: $title;
}

h3 {
  @include font-size(24px);
  font-family: $title;
}

h4 {
  @include font-size(20px);
  font-family: $title;
}

h5 {
  @include font-size(18px);
  font-family: $title;
}

h6 {
  @include font-size(16px);
  font-family: $title;
}

p,
ul,
ol {
  font-weight: 400;
  margin-top: 30px;
  margin-bottom: 0;
  line-height: 1.6em;
  display: block;
  font-family: $text;
  @include font-size(16px);
  li {
    font-weight: inherit;
    color: inherit;
    font-size: inherit;
  }

  &:empty {
    display: none;
  }

  &.center {
    display: block;
    width: 100%;
    text-align: center;
  }
}

strong {
  font-weight: 700;
}

p>a {
  transition:  all .25s ease-in-out;
  font-weight: bold;
  color: $brand;

  &:hover {
    color: darken($brand, 10%);
  }
}

:focus {
  outline: none;
}

input,
textarea {
  border: none;
  @include font-size(18px);
}

.center {
  text-align: center;
}

.container-fluid {
  padding: 0 15px;
}

.viewport-full {
  min-height: 100vh;
  width: 100vw;
}

.viewport-medium {
  min-height: 70vh;
}

.row.flex--force-stretch {
  -webkit-align-items: stretch !important;
  align-items: stretch !important;
}

.bg-purple {
  background-color: $purple;
}

.bg-orange {
  background-color: $orange;
}

.bg-yellow {
  background-color: $yellow;
}

.bg-blue {
  background-color: $blue;
}

.svg-yellow > svg > path {
  fill: $yellow;
}

.height-wrapper {
  height: 100%;
}

.row {
  &.no-margin {
    margin-left: 0;
    margin-right: 0;

    .no-padding {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .col-margin {
    margin-bottom: 15px;
    margin-top: 15px;
  }
}

#cookie_action_close_header {
  background-color: #25bdad !important;
}


.separator-custom {
  border: none;
  height: 5px;
  background-image: url(../images/dots.png);
  background-position: center;
  background-repeat: no-repeat;
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: transparent !important;
  &.separator-margin {
    margin-top: 130px;
    margin-bottom: 100px;
  }
}

.fixed-left-bottom {
  position: fixed;
  left: 15px;
  bottom: 15px;
  display: inline-block;
  z-index: 99;
}


@media (min-width: 1200px) {
  .wrapper {
    width: 90%;
    &.wrapper-left {
      margin-left: 10%;
    }
  }
}

@media (min-width: 1440px) {
  .wrapper {
    width: 80%;
    max-width: 1600px;
    &.wrapper-left {
      margin-left: 20%;
    }
  }
}


::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-thumb {
  background: #7c377c;
  border-radius: 15px;
}
::-webkit-scrollbar-thumb:hover{
  background: #592759;
}
::-webkit-scrollbar-track{
  background: #ffffff;
  border-radius: 10px;
  box-shadow: inset 7px 10px 12px #f0f0f0;
}